/* transition bar */
.top-nav-collapse {
  background-color: #555;
}

.top-nav-collapse .navbar-nav .nav-item .nav-links {
  color: #fff;
}

/* remove top navbar pad on landing page */
.top-nav-collapse .container-fluid {
  padding-top: 0px;
}

.top-nav-collapse .Navbar__nav-link--app, .collapse.show .Navbar__nav-link--app {
  background-color: #FFF;
  color: #F7931D;
  border-radius: 24px;
  cursor: pointer;
}

/* hide landing page logo on scroll */
.top-nav-collapse .Navbar__brand-image--large-image {
  display: none;
}

/* handles transition animation between landing page navbar and scrolling navbar */
@media (min-width: 768px) {
  .navbar.scrolling-navbar {
    transition: background .5s ease-in-out,padding .5s ease-in-out; /* transition for original to orange */
    /* Possible removal */
    padding-top: 16px;
    padding-bottom: 16px
  }
  .navbar.scrolling-navbar .navbar-nav>li {
    transition-duration: 1s
  }
  /* Possible removal */
  .navbar.scrolling-navbar.top-nav-collapse {
    padding: 5px 0;
  }
  .intro-margin.view {
    overflow: visible;
    margin-top: -56px;
  }
}

/* Mobile screens up to tablets */
@media only screen and (max-width: 767px) {
  .Navbar__container {
    padding-top: 0px;
  }
  .show.navbar-collapse {
    padding-bottom: 16px;
  }
  .navbar.scrolling-navbar {
    padding-top: 0px;
    padding-bottom: 0px;
    /* Resetting padding-top/bottom to remove transition effect */
  }
  .Navbar__navbar.scrolling-navbar .navbar-nav>li { transition-duration: 1s
  }
  .Navbar__navbar.scrolling-navbar.top-nav-collapse {
    /* Resetting padding-top/bottom to remove transition effect */
    padding-top: 0px;
    padding-bottom: 0px;
  }
  .intro-margin.view {
    overflow: visible;
    margin-top: -56px;
  }
  .Navbar__navbar {
    background-color: #333;
  }
  .Navbar__navbar .container-fluid {
    padding-top: 0px;
  }
  .Navbar__nav-item {
    margin-top: 8px;
  }
  .Navbar_navbar .Navbar__brand-image--mobile-image {
    display: inline;
  }
  .Navbar__navbar .Navbar__brand-image--large-image {
    display: none;
  }
  .Navbar__nav-item {
    margin-top: 6px;
  }
  .Navbar__nav-item .nav-links {
    color: #fff;
    padding: 0px 0px 0px 16px;
  }
  .Navbar__nav-item .Navbar__nav-link--app {
    padding: 4px 16px;
  }
  
  .navbar-light .navbar-toggler {
    border-color: #999;
    background-color: #f7f7f7;
  }
}

/* Medium screen sizes and above, display the full logo */
@media only screen and (min-width: 768px) {
  .navbar .Navbar__brand-image--large-image {
    display: inline;
  }
  .navbar .Navbar__brand-image--mobile-image {
    display: none;
  }
  .Navbar__nav-item .nav-links {
    padding: 4px 16px;
  }
}

/* Medium */
@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .Navbar__container {
    padding: 0px 80px;
  }
  .Navbar__brand-image--large-image {
    height: 34px;
  }
}

/* Large */
@media only screen and (min-width: 1024px) and (max-width: 1439px) {
  .Navbar__container {
    padding: 0px 80px;
  }
  .Navbar__brand-image--large-image {
    height: 48px;
  }
}

.Banner {
  position: relative;
  text-align: center;
  width: 100%;
  height: 100vh;
  background-color: #f9f9f9;
  z-index: -1;
  overflow: hidden;
}

.Banner__container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.Banner__header--main {
  color: #333;
  min-width: 600px;
  text-shadow: 1px 4px 4px rgba(0,0,0,.4), 10px 8px 13px rgba(0,0,0,.1);
  font-size: 80px;
  font-weight: 900;
  opacity: 0;
  -webkit-transform: translateX(-20px);
          transform: translateX(-20px);
  transition: all 1s linear 2s;
}

.Banner__header--sub {
  opacity: 0;
  -webkit-transform: translateX(40px);
          transform: translateX(40px);
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 400;
  color: #666;
  transition: all 1s linear 2.4s;
}

.show {
  opacity: 1;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}

.stripe {
  height: 100vh;
  top: 0;
  opacity: 0;
  position: absolute;
  background-color: #333;
  z-index: 1;
}

.stripe-0 {
  width: 20%;
  top: 0;
  left: 0;
  -webkit-animation: movedown 2.4s ease-in-out;
          animation: movedown 2.4s ease-in-out;
}

.stripe-1 {
  width: 10%;
  left: 20%;
  -webkit-animation: moveup 1.8s;
          animation: moveup 1.8s; 
}

.stripe-2 {
  width: 40%;
  left: 30%;
  -webkit-animation: movedown 2s ease-in-out;
          animation: movedown 2s ease-in-out;
}

.stripe-3 {
  width: 30%;
  left: 70%;
  -webkit-animation: moveup 2.6s ease-in-out;
          animation: moveup 2.6s ease-in-out;
}

@-webkit-keyframes movedown {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;

  }
  
  50% {
    opacity: .8;
  }
  
  100% {
    -webkit-transform: translateY(800px);
            transform: translateY(800px);
    opacity: 0;
    visibility: hidden;
  }
}

@keyframes movedown {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;

  }
  
  50% {
    opacity: .8;
  }
  
  100% {
    -webkit-transform: translateY(800px);
            transform: translateY(800px);
    opacity: 0;
    visibility: hidden;
  }
}

@-webkit-keyframes moveup {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;

  }
  
  100% {
    -webkit-transform: translateY(-800px);
            transform: translateY(-800px);
    opacity: 0;
    visibility: hidden;
  }
}

@keyframes moveup {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;

  }
  
  100% {
    -webkit-transform: translateY(-800px);
            transform: translateY(-800px);
    opacity: 0;
    visibility: hidden;
  }
}

@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  
  100% {
    opacity: 1;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  
  100% {
    opacity: 1;
  }
}
.Skills {
  max-width: 900px;
  text-align: center;
  margin: 50px auto 80px auto;
}

.Skills__container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}



.Skills__card:last-child {
  margin-right: 0;
}

.card {
  position: relative;
  margin-top: 20px;
  width: 280px;
  height: 150px;
  padding: 30px 15px;
  /*width: 33.3%;*/
  /*margin-right: 20px;*/
  box-shadow: 0 2px 4px rgba(0,0,0,.18);
  transition: all .2s linear;
}

.card__title {
  position: absolute;
  width: 120px;
  font-weight: 700;
  letter-spacing: .8px;
  color: #fff;
  border-radius: 5px;
  padding: 5px 20px;
  top: -12%;
  left: 25%;
  box-shadow: 0 2px 4px rgba(0,0,0,.18);
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.card__title::before {
  position: absolute;
  content: '';
  width: 10px;
  height: 10px;
  z-index: 1;
  top: 95%;
  left: 50%;
  -webkit-transform: rotate(45deg) translateX(-50%);
          transform: rotate(45deg) translateX(-50%);
}

.card__title-1 {
  background-color: #f7931d;
}

.card__title-1::before {
  background-color: #f7931d;
}

.card__title-2 {
  background-color: #1a82e2;
}

.card__title-2::before {
  background-color: #1a82e2;
}

.card__title-3 {
  background-color: #36d081;
}

.card__title-3::before {
  background-color: #36d081;
}


.card:last-child {
  /*margin-right: 0;*/
}

.card:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  box-shadow: 4px 4px 10px rgba(0,0,0,.1);
}
